.gutenslider-divider-top,
.gutenslider-divider-bottom {
	fill: currentColor;
	overflow: hidden;
	position: absolute;
	width: 100%;
	z-index: 5;
	pointer-events: none;

	svg {
		max-height: 100%;
		height: 100%;
		width: 100%;
	}
}

.gutenslider-divider-top {
	top: -1px;
	height: var(--gutenslider-divider-top);

	svg {
		transform: rotateX(180deg);

		&.dont-rotate-x {
			transform: rotateX(0);
		}
	}

	&.inverted svg {
		transform: rotateX(0);

		&.dont-rotate-x {
			transform: rotateX(180deg);
		}
	}

	&.flip-y svg {
		transform: rotateX(180deg) rotateY(180deg);

		&.dont-rotate-x {
			transform: rotateX(0) rotateY(180deg);
		}
	}

	&.flip-y.inverted svg {
		transform: rotateX(0) rotateY(180deg);

		&.dont-rotate-x {
			transform: rotateX(180deg) rotateY(180deg);
		}
	}
}

.gutenslider-divider-bottom {
	bottom: 0px;
	height: var(--gutenslider-divider-bottom);

	svg.dont-rotate-x {
		transform: rotateX(180deg);
	}

	&.inverted svg {
		transform: rotateX(180deg);

		&.dont-rotate-x {
			transform: rotateX(0);
		}
	}

	&.flip-y svg {
		transform: rotateY(180deg);

		&.dont-rotate-x {
			transform: rotateX(180deg) rotateY(180deg);
		}
	}

	&.flip-y.inverted svg {
		transform: rotateX(180deg) rotateY(180deg);

		&.dont-rotate-x {
			transform: rotateX(0) rotateY(180deg);
		}
	}
}
